import React, { useEffect, useState } from 'react'
import styled from 'styled-components';
import { Typography, Table, Progress, DatePicker, Space, TimePicker, Select, Image, Button, Input } from 'antd'

import { columns, data, problemTypes } from './config';
import axios from 'axios';

const { Title, Text } = Typography;
const { Option } = Select;

function ImageVideo() {
    const [imageVideos, setImageVideos] = useState([]);
    const [filterFields, setFilterFields] = useState({
        createdAt: null,
        problemType: null,
    })
    const [searchString, setSearchString] = useState();

    const onChangeDate = (date, dateString) => {
        console.log(date, dateString);
        setFilterFields({ ...filterFields, createdAt: dateString });
    }

    const onChangeProblem = (problemType) => {
        console.log("problemType: ", problemType);
        setFilterFields({ ...filterFields, problemType })
    };

    const handleSearch = () => {
        axios({
            method: "POST",
            url: "https://it4483team2.herokuapp.com/api/records/search-image-video",
            params: {
                skip: 0,
                take: 29
            },
            data: {
                title: searchString
            }
        }).then(({ data }) => {
            console.log(data);
            setImageVideos(data.result.map((i, index) => ({
                ...i,
                link: i.type === 0 ?
                    <Image
                        width={230}
                        height={150}
                        src={i.link || "error"}
                        fallback="data:image/png;base64,"
                    /> :
                    i.link,
                type: i.type === 0 ? "Image" : "Video",
                problemType: problemTypes[i.problemType],
                location: <a target="_blank" href={`https://www.google.com/maps/place/${i.latitude},${i.longitude}`} >Xem trên bản đồ</a >
            })));
        })
    };

    useEffect(() => {
        axios({
            method: "POST",
            url: "https://it4483team2.herokuapp.com/api/records/search-image-video",
            params: {
                skip: 0,
                take: 29
            },
            data: {}
        }).then(({ data }) => {
            console.log(data);
            setImageVideos(data.result.map((i, index) => ({
                ...i,
                link: i.type === 0 ?
                    <Image
                        width={230}
                        height={150}
                        src={i.link || "error"}
                        fallback="data:image/png;base64,"
                    /> :
                    i.link,
                type: i.type === 0 ? "Image" : "Video",
                problemType: problemTypes[i.problemType],
                location: <a target="_blank" href={`https://www.google.com/maps/place/${i.latitude},${i.longitude}`} >Xem trên bản đồ</a >
            })));
        })
    }, [])

    useEffect(() => {
        axios({
            method: "POST",
            url: "https://it4483team2.herokuapp.com/api/records/search-image-video",
            params: {
                skip: 0,
                take: 30
            },
            data: {}
        }).then(({ data }) => {
            let result = data.result.map((i, index) => ({
                ...i,
                link: i.type === 0 ?
                    <Image
                        width={230}
                        height={150}
                        src={i.link || "error"}
                        fallback="data:image/png;base64,"
                    /> :
                    i.link,
                type: i.type === 0 ? "Image" : "Video",
                problemType: problemTypes[i.problemType],
                location: <a target="_blank" href={`https://www.google.com/maps/place/${i.latitude},${i.longitude}`} >Xem trên bản đồ</a >
            }));

            const isFiltered = Object.values(filterFields).every(i => [null, undefined, ""].includes(i))

            if (!isFiltered) {
                Object.keys(filterFields).forEach(filterField => {
                    if (![null, undefined, ""].includes(filterFields[filterField])) {
                        console.log(filterFields);
                        console.log({ result });
                        if (`${filterField}` === "createdAt") {
                            result = result.filter(i => {
                                console.log(i[filterField].split("T"));

                                return i[filterField].split("T")[0] === filterFields[filterField]
                            });
                        }
                        else {
                            result = result.filter(i => i[filterField] === problemTypes[filterFields[filterField]]);
                        }
                    }
                });
            }

            setImageVideos(result);
        })
    }, [filterFields])

    return (
        <Container>
            <TitleCustom level={2}>Quản lý hình ảnh, video</TitleCustom>
            <SpaceCustom>
                <Space direction="horizontal">
                    <Text>Ngày:</Text>
                    <DatePicker placeholder="Chọn ngày" onChange={onChangeDate} />
                </Space>
                <Space direction="horizontal">
                    <Text>Sự cố:</Text>
                    <Select placeholder="Chọn sự cố" style={{ minWidth: 100 }} allowClear onChange={onChangeProblem}>
                        {[0, 1, 2, 3].map(problemType => <Option value={problemType}>{problemTypes[problemType]}</Option>)}
                    </Select>
                </Space>
                <Space direction="horizontal">
                    <Input
                        placeholder="Nhập tên tìm kiếm"
                        onChange={(e) => {
                            setSearchString(e.target.value)
                        }} />
                    <Button type="primary" onClick={handleSearch}>Tìm kiếm</Button>
                </Space>
            </SpaceCustom>
            <TableCustom columns={columns} dataSource={imageVideos} onChange={() => { }} pagination />
        </Container >
    )
}

const Container = styled.div`
`;

const TitleCustom = styled(Title)`
    text-align: center;
    padding: 10px 0;
`;

const SpaceCustom = styled(Space)`
    margin-bottom: 10px;
`;

const TableCustom = styled(Table)`
    overflow-x: scroll;
`;

export default ImageVideo;